import { AiOutlineUserAdd } from 'react-icons/ai';
import { BsPhone } from 'react-icons/bs';
import { RiLockPasswordLine } from 'react-icons/ri';
import { useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { useState } from 'react';
import {toast} from 'react-toastify'
import {RegisterApi, SmsApi} from 'utils/Api'

const Register = () => {
  const navigate = useNavigate();
  const { t } = useTranslation();
	const [userForm, setUserForm] = useState({
		name: '',
		phone: '',
		passwd: '',
		confirmPwd: '',
		referrer: '',
		phoneCode: '',
	})

	const handleCode = () => {
		if(userForm.phone !== ''){
		SmsApi({phone: userForm.phone}).then((res) => {
			localStorage.setItem('__web_token', res.data.token)
		})
		}
		
	}
  const handleClick = () => {
		if(userForm.passwd !== userForm.confirmPwd){
			toast.error(t('passwd_confirm_fail'))
			return false
		}
		RegisterApi(userForm).then((res) => {
			localStorage.setItem('__web_token', res.data.token)
			navigate('/person')
		}).catch(() => {
			localStorage.removeItem('__web_token')
		})

  };
	const handleInput = (e) => {
		if(e.target.name === 'phone' || e.target.name === 'referrer'){
			if(e.target.value.length <= 10){
			setUserForm({...userForm, [e.target.name]: e.target.value.replace(/[^0-9]/ig,"")})
			}
		}else{
		setUserForm({...userForm, [e.target.name]: e.target.value})
		}
	}
  return (
    <div className="h-screen flex flex-col justify-center items-center gap-4 bg-teal-300">
      <div className="w-24 h-24 rounded-full shadow flex justify-center items-center border-2 border-solid border-orange-500 mb-12 bg-white">
        logo
      </div>
				
      <div className="flex  w-11/12 h-10 rounded-md bg-white justify-start items-center ">
        <BsPhone className="ml-2 mr-2" />
        <input
          className="flex-auto outline-none"
          type="text"
					name='name'
					value={userForm.name}
          placeholder={t('nick_name')}
					onChange={handleInput}
        ></input>
      </div>

      <div className="flex  w-11/12 h-10 rounded-md bg-white justify-start items-center ">
        <BsPhone className="ml-2 mr-2" />
        <input
          className="flex-auto outline-none"
          type="text"
					name='phone'
					value={userForm.phone}
          placeholder={t('phone_number')}
					onChange={handleInput}
        ></input>
      </div>

      <div className="flex bg-white justify-start items-center w-11/12 h-10 rounded-md">
        <RiLockPasswordLine className="ml-2 mr-2" />
        <input
          type="password"
					name='passwd'
					value={userForm.passwd}
          className="flex-auto outline-none"
          placeholder={t('login_pwd')}
					onChange={handleInput}
        ></input>
      </div>

      <div className="flex bg-white justify-start items-center w-11/12 h-10 rounded-md">
        <RiLockPasswordLine className="ml-2 mr-2" />
        <input
          type="password"
					name='confirmPwd'
					value={userForm.confirmPwd}
          className="flex-auto outline-none"
          placeholder={t('confirm_pwd')}
					onChange={handleInput}
        ></input>
      </div>

      <div className="flex  w-11/12 h-10 rounded-md bg-white justify-start items-center ">
        <AiOutlineUserAdd className="ml-2 mr-2" />
        <input
          className="flex-auto outline-none"
          type="text"
					name='referrer'
					value={userForm.referrer}
          placeholder={t('agent_phone')}
					onChange={handleInput}
        ></input>
      </div>

      <div className="flex  w-11/12 justify-between items-center ">
        <input
          className=" rounded-md w-8/12 h-10 outline-none p-4"
          type="text"
					name='phoneCode'
					value={userForm.phoneCode}
          placeholder={t('verify_code')}
					onChange={handleInput}
        ></input>
        <button className="rounded-md h-10 bg-white p-2 w-1/4 text-xs text-orange-500" onClick={handleCode}>
          {t('get_verify_code')}
        </button>
      </div>

      <button
        className="w-10/12 h-12 rounded-3xl bg-white mt-4 text-orange-600 font-bold"
        onClick={handleClick}
      >
        {t('reg_now')}
      </button>
      <button
        className="w-10/12 h-12 rounded-3xl border-2 border-solid text-white font-bold"
        onClick={() => {
          navigate('/login');
        }}
      >
        {t('login_now')}
      </button>
    </div>
  );
};

export default Register;
