import http from './http'

export const SmsApi = (model) => http.post('/../common/sms', model)
export const RegisterApi = (model) => http.post('/public/register', model)
export const LoginApi = (model) => http.post('/public/login', model)
export const HomeApi = () => http.get('/public/Index')

export const PersonApi = () => http.get('/user/center')
export const WalletApi = () => http.get('/wallet')
export const PostApi = () => http.get('/info/post')
export const PostReadApi = (id) => http.delete('/info/post/' + id)
export const AboutApi = () => http.get('/info/about')

export const ShareApi = () => http.get('/info/share')

export const BuyApi = (model) => http.post('/product/buy', model)
export const DepotApi = () => http.get('/product/depot')
export const DepotReveiveApi = (model) => http.post('/product/depot', model)

export const RechargeApi = (model) => http.post('/recharge', model)

export const WithdrawCheckApi = () => http.get('/withdraw')
export const BankSaveApi = (model) => http.post('/bank', model)
export const WithdrawSubmitApi = (model) => http.post('/withdraw', model)

